@import "../global/constants.scss";

.footer{
    background: $colorBlackButton;
    display: flex;
    width: 100%;
    height: 140px;
    padding: 40px 120px;
    justify-content: space-between;
    align-items: flex-end;
    box-sizing: border-box;

    @media screen and (max-width: 800px){
            flex-direction: column;
            gap: 24px;
            align-items: flex-start;
            padding: 32px 16px;
            height: 191px;
    }

    &__contacts{
        display: flex;
        flex-direction: column;
        gap: 8px;
    }

    &__link{
        text-decoration-line: none;
        color: $colorWhite;
        border-bottom: 1px solid  rgba(white, .2);
    }


    &__text{
        color: $colorWhite;
        font-size: 18px;
        font-style: normal;
        font-weight: 500;
        line-height: 140%;

        @media screen and (max-width: 800px){
                font-size: 16px;
        }

        &--small{
            font-size: 16px;
            font-weight: 400;
            line-height: 145%;
            text-align: end;

            @media screen and (max-width: 800px){
                    text-align: start;
                    font-size: 14px;
                    line-height: 140%;
                }
        }


    }







}
